@import url('https://fonts.googleapis.com/css?family=Josefin+Sans');

* {
  margin: 0;
  font-family: Josefin;
  color: #333;
}
.navbar{
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 900px;
  margin: 0 auto;
  border-bottom: 1px solid #a9e955;
}

.navbar h1
{
  color: #1fb326;
}
.navbar .links{
  margin-left: auto;

}
.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding:  6px;
}
.navbar a:hover{
  color: #0eaf37;
}
.content{
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
}


/*blog preview*/
.blog-preview {
  padding: 10px 16px;
  margin: 10px 0;
  border-bottom: 1px solid #0000;
}

.blog-preview:hover{
  box-shadow: 1px 3px 5px #a10a0a;
}
.blog-preview h2{
  font-size: 20px;
  color: rgb(1, 117, 79);
  margin-bottom: 8px;
}

.blog-details button{
  background: #1fb326;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}
.create {
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}

.create label{
  text-align: left;
  display: block;
}

.create h2{
  font-size: 20px;
  color: #1fb326;
  margin-bottom:  30px;

}

.create input, .create textarea, .create select{
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #bbb;
  box-sizing: border-box;
  display: block1;
}
.create button{
  background:  #5e9b6d;
  border: 0;
  padding: 6px;
  border-radius: 8px;
  cursor: pointer;
}